	module.exports = {"data":{"markdownRemark":{"html":"<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 690px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 118.29436038514443%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Power and Weight Charts\"\n        title=\"\"\n        src=\"/static/example-power-and-weight-01-e20c1d3df9eb5367ccb9d5d62ce0c717-ad1f0.png\"\n        srcset=\"/static/example-power-and-weight-01-e20c1d3df9eb5367ccb9d5d62ce0c717-71707.png 173w,\n/static/example-power-and-weight-01-e20c1d3df9eb5367ccb9d5d62ce0c717-14127.png 345w,\n/static/example-power-and-weight-01-e20c1d3df9eb5367ccb9d5d62ce0c717-ad1f0.png 690w,\n/static/example-power-and-weight-01-e20c1d3df9eb5367ccb9d5d62ce0c717-6758f.png 1035w,\n/static/example-power-and-weight-01-e20c1d3df9eb5367ccb9d5d62ce0c717-c0f94.png 1380w,\n/static/example-power-and-weight-01-e20c1d3df9eb5367ccb9d5d62ce0c717-01a56.png 1454w\"\n        sizes=\"(max-width: 690px) 100vw, 690px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>When it comes to Climbing, it is all about Power and Weight, and well, Endurance, but this page focuses on Power and Weight.</p>\n<p>At this time, Strava lets you enter your current FTP (<a href=\"https://www.strava.com/settings/performance\">here</a>) and Weight (<a href=\"https://www.strava.com/settings/profile\">here</a>), but it only keeps this as a single value and does not save a history. Update your Strava numbers as necessary and A Race athlete will keep track of this for you on this page.\nWe will also calculate and track your Watts per Kilogram while we are at it.</p>\n<p>If you would like to focus in on a smaller range of data, use the slider below the charts.</p>","frontmatter":{"title":"Power and Weight","date":"2017-06-04T00:19:00.000Z","path":"/power-and-weight/","parent":"/home/hypersprite/Documents/code/araceathlete-gatsby-material-ui-v1/src/pages/2017-06-04-power-and-weight/index.md absPath of file","tags":"newbie,power,weight"}}},"pathContext":{"slug":"/2017-06-04-power-and-weight/"}}

